  <v-dialog v-model="isOpen" persistent width="500px">
    <v-card>
      <v-card-title>Choose excel file</v-card-title>
      <v-card-text>
        <v-file-input outlined dense label="Choose File" v-model="file" accept=".xls,.xlsx"></v-file-input>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn color="darken-1" text @click="onClose(); file = null">Close</v-btn>
        <v-btn color="primary darken-1" text @click="onConfirm(file); file = null" :disabled="!file">Convert</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
export default {
  name: 'import-from-excel-dialog',
  props: ['on-close', 'on-confirm', 'is-open'],
  data: ()=>({
    file: null,
  }),
};
