<template>
  <v-app>
    <right-sidebar :menuItems="networksData" serverRoute="rubix-driver"></right-sidebar>
    <v-main>
      <div class="table-styling">
        <v-tabs v-model="tab" right dark>
          <v-breadcrumbs :items="dirs" class="breadcrumbs">
            <template v-slot:item="{ item }">
              <v-breadcrumbs-item class="breadcrumbs-item-text" :to="item.link" exact> {{ item.text }} </v-breadcrumbs-item>
            </template>
          </v-breadcrumbs>
          <v-tab :key="0" style="margin-left: auto"> Data </v-tab>
          <v-tab :key="1"> Debug Messages </v-tab>
          <v-tab :key="2"> System Status </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item :key="0">
            <v-data-table
              v-model="selected"
              :headers="deviceHeaders"
              :items="network.devices"
              class="elevation-1"
              item-key="uuid"
              show-select
              :item-class="backgroundColor"
              :footer-props="tableProps.FOOTER_PROPS"
              :itemsPerPage="tableProps.ITEMS_PER_PAGE"
              :search="search"
              :loading="loadingData"
            >
              <template v-slot:top>
                <v-toolbar flat>
                  <v-toolbar-title>DEVICES</v-toolbar-title>
                  <v-divider class="mx-4" inset vertical></v-divider>
                  <div class="text-left pa-2">
                    <v-btn @click="onClickRefresh" x-small color="primary" class="white--text">Refresh<v-icon x-small right dark> mdi-refresh </v-icon> </v-btn >&nbsp;
                    <v-btn @click="dialog = true; tags = []" x-small color="success" class="white--text">Create<v-icon x-small right dark> mdi-plus </v-icon> </v-btn>&nbsp;
                    <v-btn @click="clickDeleteMulti" :disabled="selected.length === 0" x-small color="error" class="white--text" >Delete<v-icon x-small right dark> mdi-delete </v-icon> </v-btn>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="#009688" class="white--text" dark v-bind="attrs" v-on="on">Download<v-icon x-small right dark>mdi-download</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'json')">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'excel')">Excel</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="clickDownload(network.devices, 'csv')">CSV</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </v-menu>&nbsp;
                    <v-menu offset-y>
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn x-small color="primary" class="white--text" v-bind="attrs" v-on="on">Import<v-icon x-small right dark>mdi-import</v-icon></v-btn>
                      </template>
                      <v-list>
                        <v-list-item link>
                          <v-list-item-title @click="clickImportJson">Json</v-list-item-title>
                        </v-list-item>
                        <v-list-item link>
                          <v-list-item-title @click="dialogImportExcel = true">Excel</v-list-item-title  >
                        </v-list-item>
                      </v-list>
                    </v-menu >&nbsp;
                    <v-btn @click="clickJsonExport(selected)" :disabled="selected.length === 0" x-small color="#009688" class="white--text" > Export<v-icon x-small right dark> mdi-export </v-icon> </v-btn>&nbsp;
                  </div>
                  <v-spacer></v-spacer>
                  <v-text-field v-model="search" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                </v-toolbar>
              </template>

              <template v-slot:item.actions="{ item }">
                <v-icon small @click="onClickEditItem(item)" color="primary">mdi-pencil</v-icon>
                <v-icon small @click="clickDelete(item)" color="error">mdi-delete</v-icon>
                <v-icon small @click="onClickOpen(item)">mdi-open-in-app</v-icon>
              </template>
              <template v-slot:item.created_on="{ item }" >
                {{item.created_on | timezone}}
              </template>
              <template v-slot:item.updated_on="{ item }" >
                {{item.updated_on | timezone}}
              </template>
              <template v-slot:item.name="{ item }">
                <a @click="onClickOpen(item)"> {{ item.name }} </a>
              </template>
              <template v-slot:item.history_enable="{ item }">
                <v-checkbox v-model="item.history_enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:item.enable="{ item }">
                <v-checkbox v-model="item.enable" @click="updateDevice(item)"></v-checkbox>
              </template>
              <template v-slot:item.tags="{ item }">
                <ul style="cursor: pointer;" class="overflow-td" @click="onClickEditItem(item)" >
                  <li v-for="(key, index) of item.tags ? Object.keys(JSON.parse(item.tags)): []" :key="index">
                    {{key}} : {{JSON.parse(item.tags)[key]}}
                  </li>
                </ul>
              </template>

              <!-- headers -->
              <template v-slot:header.enable="{ item }">
                {{ toUpperTextKey('enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.history_enable="{ item }">
                {{ toUpperTextKey('history_enable') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('history_enable'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>
              <template v-slot:header.tags="{ item }">
                {{ toUpperTextKey('tags') }}&nbsp;
                <v-icon x-small @click="openMassEditDialog('tags'), $event.stopPropagation()" color="primary">mdi-pencil</v-icon>&nbsp;
              </template>

              <template v-slot:no-data> No data </template>
            </v-data-table>
          </v-tab-item>
          <v-tab-item :key="1">
            <mqtt-console :editable="false" :subscriptions="debugSubscriptions"></mqtt-console>
          </v-tab-item>
          <v-tab-item :key="2">
            <system-status-table server='modbus'></system-status-table>
          </v-tab-item>
        </v-tabs-items>
      </div>
    </v-main>

    <!-- Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="500px">
      <v-card>
        <v-card-title>
          <span class="headline">{{ formTitle }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="network.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field v-model="editedItem.name" label="device name" required :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-checkbox v-model="editedItem.history_enable" label="History Enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <v-text-field  value="Tags" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="12" md="12">
                  <rd-point-tags v-if="dialog" :on-change="onChangeTag" :items="tags"></rd-point-tags>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close">Cancel</v-btn>
          <v-btn color="blue darken-1" text @click="save" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat dark>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon dark @click="closeDialogImport"> <v-icon>mdi-close</v-icon> </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="deviceHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field v-model="searchWizard" append-icon="mdi-magnify" label="Search" single-line hide-details ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:item.actions="{ item }">
                  <v-icon small color="error" @click="clickDeleteImport(item)"> mdi-delete </v-icon>
                  <!-- <v-icon small color="error" v-if="isClickSaveImport && !isExistingvalue(item.uuid, 'uuid')">mdi-alert-octagram-outline</v-icon> -->
                  <v-icon small color="success" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-check-circle-outline</v-icon>
                  <v-icon small color="info" @click="createDevice(item)" v-if="isClickSaveImport" :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')">mdi-cached</v-icon>
                </template>

                <template v-slot:item.name="{ item }">
                  <v-text-field v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')" v-model="item.name" single-line style="width: 100px" disabled></v-text-field>
                  <v-text-field v-else v-model="item.name" single-line style="width: 100px" required :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog :is-open="dialogImportExcel" :on-close="()=>dialogImportExcel = false" :on-confirm="convertExcelToJson"></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog :is-open="dialogDelete" :on-close="closeDelete" :on-confirm="confirm" dialog-text="Do you want to delete ?"></confirm-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="300px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-checkbox v-if="typeof editAll[editField] == 'boolean'" v-model="editAll[editField]" :label="toUpperTextKey(editField)" small/>
          <rd-point-tags v-if="editField === 'tags'" :on-change="onChangeTag" :items="tags"></rd-point-tags>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary darken-1" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

  </v-app>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import _cloneDeep from 'lodash/cloneDeep';
import _find from 'lodash/find';
import _forIn from 'lodash/forIn';
import _forEach from 'lodash/forEach';
import MqttConsole from '../Mqtt/MqttConsole';
import RightSidebar from '../Common/RightSidebar';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import SystemStatusTable from '../Common/SystemStatusTable.vue';
import RdPointTags from './RdPointTags'
import * as changeCase from "change-case";
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import { RULES, TOPIC, DEVICE_DEFAULT, DEVICE_HEADERS, DEVICE_EDIT_ALL_DEFAULT, } from './rubixDriverConstant';
import { copyToClipboard, isJsonString, exportJSON, exportCSV, exportExcel, toUpperTextKey } from '../../utils/utils';

export default {
  components: { 
    RightSidebar, 
    MqttConsole, 
    ConfirmDialog, 
    ImportFromExcelDialog, 
    RdPointTags,
    SystemStatusTable
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    topic: TOPIC,
    rules: {...RULES},
    tableProps: {...DATA_TABLE_PROPS},
    deviceHeaders: [...DEVICE_HEADERS],
    editedItem: {...DEVICE_DEFAULT},
    editAll:{...DEVICE_EDIT_ALL_DEFAULT},
    dialog: false,
    dialogDelete: false,
    dialogImport: false,
    dialogImportExcel: false,
    dialogMassEdit: false,
    tab: 0,
    valid: false,
    editedIndex: -1,
    network: {},
    selected: [],
    action: '',
    dirs: [],
    dataImport: '',
    importArray: [],
    e1: 1,
    allPoints: [],
    search: '',
    searchWizard: '',
    editField : "",
    intervalId: null,
    devicesIncludeChild: [],
    isClickSaveImport: false,
    tags: [],
    loadingData: false,
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    networksData () {
      return this.$store.state.rdNetworks;
    },
    debugSubscriptions () {
      const { debugSubscriptions } = this.$store.state.mqttConfig;
      return debugSubscriptions.driverRubix;
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
    '$route.params.id':function(val, oldVal){
      if(val != oldVal){
        this.getData();
      }
    },
    tags(val) {
      this.onChangeTag(val);
    },
  },
  beforeRouteLeave (to, from, next) {
    clearInterval(this.intervalId);
    axios.post(`mqtt/unsubscribe`, {topic: this.topic}).finally(() => {
      next();
    });
  },
  mounted() {
    this.getData();

    //set auto refresh
    if(this.setting.refreshTime &&  this.setting.enableAutoRefresh) {
      this.intervalId = setInterval(()=>{
        this.getData();
        if(this.setting.toastAutoRefresh){
          this.$toasted.show(`Automatically refresh every ${this.setting.refreshTime}ms`, { type: 'info' });
        }
      }, this.setting.refreshTime);
    }
  },
  methods: {
    getData() {
      this.loadingData = true;
      axios.get(`point-server/generic/network/${this.$route.params.id}?with_children=true`).then(data => {
        this.network = data;
        this.getDirs();
        this.editedItem.network_uuid = this.network.uuid;
        this.updateRdNetworksData(this.network);
      }).finally(() => {
        this.loadingData = false;
      });
    },
    getDirs(){
      this.dirs = [{ text: "NETWORKS", link: '/rubix-driver/networks'}];
      this.dirs.push({ text: `NET: ${this.network.name}` });
    },
    async getDeviceIncludeChild(item) {
      const device = await axios.get(`point-server/generic/device/${item.uuid}?with_children=true`);
      this.devicesIncludeChild.push(device);
    },
    onClickOpen(item) {
      this.$router.push({ path: `/rubix-driver/devices/${item.uuid}/points`});
    },
    onClickEditItem(item) {
      this.editedIndex = this.network.devices.indexOf(item);
      this.editedItem = Object.assign({}, item);
      const arrTags = [];
      if (item.tags) {
        const tags = JSON.parse(item.tags);
        for (const tag in tags) {
          arrTags.push({key: tag, value: tags[tag]});
        }
      }
      this.tags = arrTags;
      this.dialog = true;
    },
    async save() {
      if (this.$refs.form.validate()) {
        if (this.editedIndex > -1) {
          this.updateDevice(this.editedItem);
        } else {
          this.createDevice(this.editedItem);
        }
      }
    },
    async clickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async clickDeleteMulti() {
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      this.editedIndex = this.network.devices.indexOf(this.editedItem);
      this.network.devices.splice(this.editedIndex, 1);
      await axios.delete(`point-server/generic/device/${this.editedItem.uuid}`);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, {...DEVICE_DEFAULT, network_uuid: this.network.uuid});
        this.editedIndex = -1;
      });
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, {...DEVICE_DEFAULT, network_uuid: this.network.uuid});
        this.editedIndex = -1;
      });
    },
    async clickImportJson() {
      this.devicesIncludeChild = [];
      this.dialogImport = true;
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.devicesIncludeChild) : '';
      this.e1 = 1;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        workbook.SheetNames.forEach(sheet => {
          let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets[sheet]);
          const items = rowObject.map(i => {
            for (let key in i) {
              const newKey = key.trimRight('');
              const value = i[key];
              delete i[key];
              i = { ...i, [newKey]: value };
            }
            i.enable = i.enable ? i.enable : false;
            i.history_enable = i.history_enable ? i.history_enable : false;
            i.network_uuid = this.network.uuid;
            i = this.getPointsArray(i);
            return i;
          });
          this.dataImport = JSON.stringify(items);
          this.dialogImportExcel = false;
          this.dialogImport = true;
          this.e1 = 1;
        });
      };
    },
    async clickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          try{
            this.isClickSaveImport = true;
            const promises = [];
            for (const item of this.importArray) {
              if(!item.uuid){
                item.network_uuid = this.network.uuid;  //update to the current device_uuid
                promises.push(new Promise((resolve, reject) => this.createDevice(item, resolve, reject)));
              }
            }
            await Promise.all(promises);
            this.closeDialogImport();
            this.selected = [];
          }catch(ex){}
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(device=>{
        delete device.uuid;
        return device;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName) {
      const devices = _cloneDeep(this.network.devices);
      if (this.editedIndex !== -1) {
        //when edit
        devices.splice(this.editedIndex, 1);
      }
      if (fieldName === 'address' || fieldName === 'tcp_port') {
        value = Number(value);
      }
      return _find(devices, { [fieldName]: value }) != null;
    },
    async clickJsonExport() {
      this.devicesIncludeChild = [];
      const promises = [];
      for (const item of this.selected) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      const status = copyToClipboard(JSON.stringify(this.devicesIncludeChild));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    async clickDownload(array, type) {
      this.devicesIncludeChild = [];
      const promises = [];
      for (const item of array) {
        promises.push(this.getDeviceIncludeChild(item));
      }
      await Promise.all(promises);
      const data = this.convertToExportData(this.devicesIncludeChild);
      if (type === 'csv') {
        return exportCSV('devices', data);
      }
      if (type === 'excel') {
        return exportExcel('devices', data);
      }
      if (type === 'json') {
        return exportJSON('devices', JSON.stringify(this.devicesIncludeChild));
      }
    },
    convertToExportData(array) {
      const data = _cloneDeep(array).map(device => {
        _forIn(device, (value, key) => {
          if (key == 'points') {
            //get points
            _forEach(device.points, (point, indexP) => {
              _forIn(point, (point_value, point_key) => {
                if (point_key == 'priority_array_write') {
                  point_value = JSON.stringify(point_value);
                }
                if (point_key != 'point_store') {
                  const pointKey = `point[${indexP}].${point_key}`;
                  device = { ...device, [pointKey]: point_value };
                } else {
                  //point_store
                  _forIn(point.point_store, (point_store_value, point_store_key) => {
                    const pointStoreKey = `point[${indexP}].point_store.${point_store_key}`;
                    device = { ...device, [pointStoreKey]: point_store_value };
                  });
                  delete point.point_store;
                }
              });
            });
            delete device.points;
          }
        });
        return device;
      });
      return data;
    },
    getPointsArray(device) {
      let points = [];
      let indexs = [];
      let pointKeys = [];
      Object.keys(device).forEach(key => {
        let keys = key.split('.');
        let pointKey = '';
        let pointIndex = null;
        if (keys.length > 1) {
          pointKey = keys[1];
          if (!pointKeys.includes(pointKey)) {
            pointKeys.push(pointKey);
          }
          pointIndex = Number(keys[0].split('[')[1].slice(0, -1));
          if ((pointIndex || pointIndex === 0) && !indexs.includes(pointIndex)) {
            indexs.push(pointIndex);
          }
        }
      });
      _forEach(indexs, i => {
        let point = {};
        _forEach(pointKeys, pk => {
          point = { ...point, [pk]: device[`point[${i}].${pk}`] };
        });
        point.priority_array_write = JSON.parse(point.priority_array_write);
        points.push(point);
      });
      device.points = points;
      return device;
    },
    updateDevice(data) {
      data.name = this.setting.enableChangeCase ? changeCase[this.setting.caseType](data.name) : data.name;
      axios
        .put(`point-server/generic/device/${data.uuid}`, data)
        .then(response => {
          const index = this.network.devices.findIndex(d => d.uuid == response.uuid);
          Object.assign(this.network.devices[index], response);
          this.close();
          return this.$toasted.show('update success');
        })
        .catch(error => {
          this.$toasted.show('update fail', { type: 'error' });
        });
    },
    async createDevice(data, resolve, reject) {
      try {
        data.name = this.setting.enableChangeCase ? changeCase[this.setting.caseType](data.name) : data.name;
        const response = await axios.post(`point-server/generic/device`, data);
        this.network.devices.push(response);
        if (this.importArray && this.importArray.length > 0) {
          const index = this.importArray.findIndex(n => n.name == response.name);
          if(index > -1){
            Object.assign(this.importArray[index], response);
          }
        }
        if (data.points && data.points.length > 0) {
          const promises = [];
          for (const point of data.points) {
            point.device_uuid = response.uuid;
            point.register = point.register ? point.register : 0;
            point.register_length = point.register_length ? point.register_length : 0;
            point.enable = point.enable ? point.enable : false;
            promises.push(this.createPoint(point));
          }
          await Promise.all(promises);
        }
        this.close();
        this.$toasted.show('create success');
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    createPoint(data) {
      return axios.post(`point-server/generic/point`, data)
    },
    backgroundColor(item) {
      let classColor = '';
      if (item.fault) {
        classColor = 'background-fault-true';
      } else {
        classColor = !item.enable ? 'background-enable-false' : '';
      }
      return classColor;
    },
    saveMassEdit (value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updateDevice(item);
      }
      this.dialogMassEdit = false;
    },
    async openMassEditDialog(fieldName){
      if(this.selected.length < 1){
        return this.$toasted.show("Please select item first!", {type: 'error'});
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = {...DEVICE_EDIT_ALL_DEFAULT};
      this.tags = [];
    },
    updateRdNetworksData(newNetwork){
      const indexNetWork = this.networksData.findIndex(nw=>nw.uuid == newNetwork.uuid);
      if(indexNetWork > -1){
        Object.assign(this.networksData[indexNetWork], newNetwork);
        this.$store.commit('changeRdNetworksData', this.networksData);
      }
    },
    onClickRefresh(){
      this.getData();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    onChangeTag(tags) {
      if(tags.length == 0) return;
      let strTags = '';
      if (Array.isArray(tags)) {
        const objTags = {};
        for (const tag of tags) {
          if (tag.key && tag.value) {
            objTags[tag.key] = tag.value;
          }
        }
        strTags = JSON.stringify({...objTags});
      } else {
        strTags = JSON.stringify(tags);
      }
      this.editAll[this.editField] = strTags;
      this.editedItem.tags = strTags;
    },
  },
};
</script>
